<template>
  <div class="mainXiaoGuo">
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a @click="bus.$emit('showDrawer')" class="icon icon_manue"> manue </a>
      </div>
      <h1>{{ productDetail.name }}</h1>
      <div class="chartWrap">
        <span class="share iconfont" @click="shareShow()"> &#xe650; </span>
        <span class="bag" v-if="indexData.shoppingCartNum > 0">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a
          href="javascript:void(0)"
          @click="$router.push('cart')"
          class="iconfont cart"
        >
          &#xe669;
        </a>
      </div>
    </section>

    <section
      class="detailImg"
      :class="{ timerIn: productDetail.flashType == 1 }"
    >
      <div class="slideWrap">
        <swiper :options="bannerSwiperOption">
          <swiper-slide
            v-for="(item, index) in productDetail.spuMainImageList"
            :key="index"
          >
            <img class="img" :src="item" alt="" />
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <span
        style="z-index:10"
        :class="[
          'icon',
          'icon_like',
          wishCode.indexOf(productDetail.spuCode) > -1 ? 'active_like' : ''
        ]"
        @click="handlerLove({ spuCode: productDetail.spuCode })"
      >
      </span>
      <div class="downTimer" v-if="productDetail.flashType == 1">
        <div>{{ productDetail.activeName }}</div>
        <div>
          Ends in
          <div class="timer">
            <span> {{ timer.h }} </span> : <span> {{ timer.m }} </span> :
            <span> {{ timer.s }} </span>
          </div>
        </div>
      </div>
    </section>

    <section class="proContent">
      <div class="blocks top">
        <p class="name">
          {{ productDetail.name }}
        </p>
        <div class="box-flex justify-between">
          <div>
            <p>
              <span class="price" v-if="selectSku">
                {{
                  (
                    (isVIP ? selectSku.merchantPrice : selectSku.mallPrice) /
                    100
                  ).toFixed(2)
                }}
                AED
              </span>
              <span class="price" v-if="!selectSku">
                {{
                  isVIP ? productDetail.vipPriceRange : productDetail.priceRange
                }}
                AED
              </span>
            </p>
            <p>
              <span v-if="isVIP" class="tips_vip"> VIP </span>
              <span
                class="tips_cutOff"
                v-if="
                  isVIP
                    ? productDetail.vipPercentage
                    : productDetail.percentage != '0%'
                "
              >
                {{
                  isVIP ? productDetail.vipPercentage : productDetail.percentage
                }}
                OFF
              </span>
            </p>
            <p v-if="productDetail.priceRange != productDetail.priceRangeLine">
              <del v-if="selectSku">
                {{ (selectSku.price / 100).toFixed(2) }} AED
              </del>
              <del v-if="!selectSku">
                {{ productDetail.priceRangeLine }} AED
              </del>
            </p>
            <p class="sign">
              <span v-if="productDetail.isTypeFour == 1"> Free Shipping </span>
              <span v-if="productDetail.isTypeFour == 1"> | </span>
              <span> Inclusive of(VAT) </span>
            </p>
          </div>
          <!-- 查看评论列表 -->
          <div class="starWrap" @click="commentGo">
            <div class="star">
              <!-- 星星展示 -->
              <span
                class="iconfont"
                :class="{ active: i <= productDetail.commentPoints }"
                v-for="i in 5"
                :key="i"
              >
                <template v-if="i + 0.5 != productDetail.commentPoints">
                  <em> &#xe65f; </em>
                </template>
                <em
                  v-if="i + 0.5 == productDetail.commentPoints"
                  class="icon iconHalfStar"
                >
                </em>
              </span>
            </div>
            <p class="blue mt5">{{ productDetail.commentCount }} Reviews</p>
          </div>
        </div>
      </div>

      <!-- 规格选择 -->
      <div class="blocks sizeChoose mt10" id="anchor">
        <div>
          <template v-for="(item, index) in productDetail.saleAttr">
            <p class="tittle">
              {{ item.name }} <span class="color-res" v-if="false">only 5</span>
            </p>
            <p class="blocks">
              <a
                :class="{ active: i == item.selectIndex, disable: p.disable }"
                v-for="(p, i) in item.productAttributeOptionList"
                :key="i"
                href="javascript:void(0)"
                @click="selectAttribute(index, i, item, p)"
              >
                {{ p.name }}
              </a>
            </p>
          </template>
        </div>

        <p class="tittle">Number</p>
        <div class="gw_num clearfix">
          <em class="jian" @click="jian">  </em>
          <input type="number" v-model="num" class="num" />
          <em class="add" @click="jia"> </em>
        </div>
      </div>
      <div class="blocks shipto mt10">
        <p>
          Ship to
          {{ countryCode ? countryCode.address : productDetail.address }} (
          <span
            class="blue"
            @click="$router.push({ path: 'map', query: { type: 2 } })"
          >
            Change Location
          </span>
          )
        </p>
        <p>
          Delivered within
          <span class="black">
            {{ formateTime(latestDay || productDetail.latestDay) }}
          </span>
        </p>
      </div>

      <div class="blocks mt10 Description" v-if="!!productDetail.proDetail">
        <p class="tittle">Description</p>
        <div class="content" :class="{ open: DescriptionOpen }">
          {{ productDetail.proDetail }}
        </div>
        <p :class="{ toggle: DescriptionOpen, toggleClose: !DescriptionOpen }">
          <span class="blue" @click="DescriptionOpen = !DescriptionOpen">
            {{ !DescriptionOpen ? "See More" : "Show Less" }}
          </span>
        </p>
      </div>

      <div
        class="blocks mt10 Specifications"
        v-if="!!productDetail.secondAttr && productDetail.secondAttr.length > 0"
      >
        <p class="tittle">Specifications</p>
        <div class="content" :class="{ open: SpecificationsOpen }">
          <p v-for="(item, index) in productDetail.secondAttr" :key="index">
            <label> {{ item.attrName }} </label>
            <span> {{ item.optName }}</span>
          </p>
        </div>
        <p
          :class="{
            toggle: SpecificationsOpen,
            toggleClose: !SpecificationsOpen
          }"
        >
          <span class="blue" @click="SpecificationsOpen = !SpecificationsOpen">
            {{ !SpecificationsOpen ? "See More" : "Show Less" }}
          </span>
        </p>
      </div>

      <div
        class="mt10 blocks Reviews"
        v-if="
          !!productDetail.commentTen && productDetail.commentTen.length != 0
        "
      >
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Reviews</h3>
          </div>
          <!-- 查看所有推荐 -->
          <div>
            <a
              href="javascript:void(0)"
              @click="
                $router.push({
                  path: 'reviewsList',
                  query: { spuCode: spuCode }
                })
              "
              class="blue"
            >
              See All</a
            >
          </div>
        </div>
        <div class="content">
          <swiper :options="reviewsSwiperOption">
            <swiper-slide
              v-for="(item, index) in productDetail.commentTen"
              :key="index"
            >
              <div class="user box-flex">
                <span class="userName"> {{ item.nickName }} </span>
                <div class="star">
                  <span
                    class="iconfont"
                    :class="{ active: i <= item.points }"
                    v-for="i in 5"
                    :key="i"
                  >
                    <template v-if="i + 0.5 != item.points">
                      <em> &#xe65f; </em>
                    </template>
                    <em v-if="i + 0.5 == item.points" class="icon iconHalfStar">
                    </em>
                  </span>
                </div>
              </div>
              <div class="says">
                {{ item.comment }}
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>

      <section class="salesWrap">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Best sales of this Category</h3>
          </div>
          <div>
            <a
              @click="
                $router.push({
                  path: '/searchResult',
                  query: { category2Code: productDetail.category2Code }
                })
              "
              class="blue"
            >
              See All</a
            >
          </div>
        </div>
        <div class="commonPro" style="padding-bottom: 30px;">
          <swiper
            :options="commonSwiperOption"
            ref="bestShareSwiper"
            @click="chooseDetail"
          >
            <swiper-slide
              v-for="(item, index) in productDetail.proListCate2"
              :key="index"
            >
              <div class="item">
                <div
                  class="tips_cutOff"
                  v-if="isVIP ? item.vipPercentage : item.percentage != '0%'"
                >
                  {{ isVIP ? item.vipPercentage : item.percentage }} OFF
                </div>

                <div
                  class="tips_love iconfont"
                  @click.stop="handlerLove(item)"
                  :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                >
                  {{
                    wishCode.indexOf(item.spuCode) > -1
                      ? "&#xe653;"
                      : "&#xe652;"
                  }}
                </div>
                <div class="tips_sale" v-if="item.sale"></div>
                <img class="img" :src="item.spuMainImage" alt="" />
              </div>
              <div class="name">{{ item.name }}</div>
              <div class="bottomPrice">
                <p class="red">
                  {{ getStr(isVIP ? item.vipPriceRange : item.priceRange) }} AED
                </p>
                <p
                  class="gray"
                  v-if="
                    getStr(isVIP ? item.vipPriceRange : item.priceRange) !=
                      getStr(item.priceRangeLine)
                  "
                >
                  {{ getStr(item.priceRangeLine) }} AED
                </p>
                <p v-if="item.isTypeFour == 1">Free Shipping</p>
              </div>
              <div class="downShare">
                <div :class="{ active: item.share }" @click="shareShow(item)">
                  <span class="share iconfont"> &#xe650; </span>
                </div>
                <div :class="{ active: item.cart }" @click="addChart(item)">
                  <span class="cart iconfont"> &#xe669; </span>
                </div>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </section>
    </section>

    <div class="downBtnWrap">
      <van-button type="danger" @click="addCart">Add to Cart</van-button>
      <van-button type="danger" @click="addCartGo"
        >Add and Go to Cart</van-button
      >
    </div>

    <!-- 分享弹窗 -->
    <share-model
      :shareBol="shareBol"
      @shareHide="shareHide"
      @mangban="shareHide"
    ></share-model>
    <!-- 回到顶部 -->
    <div class="go-top iconfont" v-if="btnFlag" @click="backTop">
      &#xe612;
    </div>

    <!-- 添加购物车 -->
    <add-cart ref="addCart"> </add-cart>
  </div>
</template>

<script>
import shareModel from "./share/shareModel.vue";
import { mapGetters } from "vuex";
import { getProListByName, getlatestDayByCountryCode } from "@/api/product";
export default {
  name: "proDetail",
  components: { shareModel },
  data() {
    return {
      shareBol: false,
      btnFlag: false,
      skuCode: null,
      num: 1,
      hasTimer: false,
      colorSelect: null,
      sizeSelect: null,
      spuCode: this.$route.query.spuCode,
      bannerSwiperOption: {
        autoplay: true,
        loop: true,
        slidesPerView: 1,
        spaceBetween: 0,
        centeredSlides: true,
        pagination: {
          el: ".swiper-pagination",
          type: "fraction"
        }
      },
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true
      },
      reviewsSwiperOption: {
        loop: true,
        slidesPerView: 1.2,
        spaceBetween: 10,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00",
        remain: 0
      },
      starNum: 4.5,
      isVIP: false,
      DescriptionOpen: false,
      SpecificationsOpen: false,
      productDetail: {},
      attributeList: [],
      atrributeMap: {},
      selectIndex: [0, "-", 0],
      selectSku: null,
      latestDay: null
    };
  },
  computed: {
    ...mapGetters(["user", "indexData", "wishCode", "wishes", "countryCode"])
  },
  mounted() {
    //window.scrollBy(0,-10);
    window.addEventListener("scroll", this.scrollToTop);
    // this.bus.$emit('showLoading');
    this.init();
    this.isVIP = this.user && this.user.level * 1 > 1;
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },

  methods: {
    init() {
      this.$store
        .dispatch("getProDetailBySpuCode", { spuCode: this.spuCode })
        .then(res => {
          //this.bus.$emit('hideLoading');
          this.productDetail = res;
          this.$store.dispatch("addBrowserRecord", {
            product: this.productDetail
          });
          if (this.productDetail.flashType == 1) {
            this.timer.remain = this.productDetail.endTimeStr * 1;
            this.countTime();
          }
          let i = 0;
          this.productDetail.productSkuList.map(t => {
            let attrJson = JSON.parse(t.attributeJson);

            let selectKey = [];
            attrJson.forEach(t => {
              for (const key in t) {
                selectKey.push(t[key]);
              }
            });

            this.attributeList.push(selectKey.join("-"));
            this.atrributeMap[selectKey.join("-")] = t;
          });
          if (this.$route.query.from == "falshSale") {
            this.selectDefaultSku();
          } else {
            this.setDisabled();
          }
          // this.countTime();
        })
        .catch(e => {
          console.log(e);
        });

      if (this.countryCode) {
        getlatestDayByCountryCode({ cityCode: this.countryCode.cityCode }).then(
          res => {
            this.latestDay = res.data.result.latestDay;
          }
        );
      }
    },
    countTime() {
      let leftTime = this.timer.remain;
      if (leftTime >= 0) {
        this.timer.h = this.timeStr(
          Math.floor(leftTime / 60 / 60) > 99
            ? 99
            : Math.floor(leftTime / 60 / 60)
        );
        this.timer.m = this.timeStr(Math.floor((leftTime / 60) % 60));
        this.timer.s = this.timeStr(Math.floor(leftTime % 60));
        this.timer.remain = this.timer.remain - 1;
      }

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    goBack() {
      this.$router.back(-1);
    },
    chooseDetail(e) {
      let index = this.$refs.bestShareSwiper.swiper.clickedIndex;
      this.goDetail(this.productDetail.proListCate2[index].spuCode);
    },
    goDetail(spuCode) {
      this.$router.push({
        path: "/proDetail",
        query: { spuCode: spuCode }
      });
    },
    //评论列表
    commentGo() {
      this.$router.push({
        path: "/reviewsList",
        query: {
          spuCode: this.spuCode
        }
      });
    },
    jia() {
      this.num++;
      if (this.selectSku) {
        if (this.num > this.selectSku.stock) {
          this.num = this.selectSku.stock;
          this.$toast("Your amount is out of stock");
        }
      }
    },
    jian() {
      this.num--;
      if (this.num <= 1) {
        this.num = 1;
      }
    },
    selectAttribute(index, itemIndex, attr, item) {
      if (item.disable) {
        this.$toast("Your amount is out of stock");
        return false;
      }

      this.$set(
        this.selectIndex,
        [index == 0 ? 0 : 2],
        attr.selectIndex == itemIndex ? "" : item.id
      );
      let sIndex = this.selectIndex.join("");

      this.$set(
        attr,
        "selectIndex",
        attr.selectIndex == itemIndex ? "-1" : itemIndex
      );

      if (this.selectIndex[0] > 0 && this.selectIndex[2] > 0) {
        this.selectSku = this.atrributeMap[sIndex];
      } else {
        this.selectSku = null;
      }

      // 检测数据
      let searchValue =
        (index == 0 ? item.id : "") + "-" + (index == 1 ? item.id : "");
      let arr = this.attributeList.filter(t => {
        return t.indexOf(searchValue) > -1;
      });
      // 默认全黑
      let otherIndex = index == 0 ? 1 : 0;
      this.productDetail.saleAttr[
        otherIndex
      ].productAttributeOptionList.forEach(s => {
        this.$set(s, "disable", true);
      });
      if (arr.length > 0) {
        arr.forEach(a => {
          let optionId = a.split("-")[otherIndex];
          let selectResult = this.atrributeMap[a];

          if (selectResult.stock > 0) {
            this.productDetail.saleAttr[
              otherIndex
            ].productAttributeOptionList.forEach(s => {
              if (s.id == optionId) {
                this.$set(s, "disable", false);
              }
            });
          }
        });
      }
    },
    addCartCom(isToCart) {
      if (this.selectSku == null) {
        this.$toast("Please Select Specification");
        const returnEle = document.querySelector("#anchor");
        if (!!returnEle) {
          returnEle.scrollIntoView(true);
        }
      } else {
        this.$store
          .dispatch("addCart", {
            skuCode: this.selectSku.skuCode,
            num: this.num
          })
          .then(res => {
            this.bus.$emit("loadIndexData");
            this.$toast("Add Successfully");
            if (isToCart) {
              this.$router.push("/cart");
            }
          });
      }
    },
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    addCart() {
      this.addCartCom();
    },
    addCartGo() {
      this.addCartCom(true);
    },
    //分享弹窗
    shareShow(item) {
      if (!item) {
        item = {
          spuMainImage: this.productDetail.spuMainImageList[0],
          spuCode: this.productDetail.spuCode,
          name: this.productDetail.name
        };
      }
      this.$store
        .dispatch("share", {
          imageUrl: [item.spuMainImage],
          url: "proDetail?spuCode=" + item.spuCode,
          title: item.name
        })
        .then(res => {});
    },
    shareHide() {
      this.shareBol = false;
    },
    // 回到顶部
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      let that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 100) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
    getStr(obj) {
      var index = obj ? obj.indexOf("-") : -1;
      if (index != -1) {
        var str = obj.substring(0, index - 1);
        return str;
      } else {
        return obj;
      }
    },
    formateTime(leftDay) {
      let date = new Date();
      date.setDate(date.getDate() + leftDay);
      return date.Format("dd/MM/yyyy");
    },
    handlerLove(item) {
      let index = this.wishCode.indexOf(item.spuCode);
      let isAdd = index == -1;
      this.$store
        .dispatch(
          isAdd ? "addWish" : "deleteWish",
          isAdd
            ? { type: "spu", code: item.spuCode }
            : { id: this.wishes.result[index].id }
        )
        .then(res => {
          if (isAdd) {
            this.$toast("Move to Whislist Successfully");
          } else {
            this.$toast("Remove From Whislist Successfully");
          }
          this.$store.dispatch("myWishes");
        });
    },
    selectDefaultSku() {
      let index = -1,
        select;
      for (let i = 0; i < this.productDetail.productSkuList.length; i++) {
        const element = this.productDetail.productSkuList[i];
        if (element.stock > 0) {
          select = element;
          index = i;
          break;
        }
      }

      if (index > -1) {
        this.selectSku = select;
        let selectKey = [];
        JSON.parse(this.selectSku.attributeJson).forEach(t => {
          for (const key in t) {
            selectKey.push(t[key]);
          }
        });

        let firstIndex = this.productDetail.saleAttr[0].productAttributeOptionList
          .map(t => {
            return t.id;
          })
          .indexOf(selectKey[0] * 1);
        let secondIndex = this.productDetail.saleAttr[1].productAttributeOptionList
          .map(t => {
            return t.id;
          })
          .indexOf(selectKey[1] * 1);
        this.selectAttribute(
          0,
          firstIndex,
          this.productDetail.saleAttr[0],
          this.productDetail.saleAttr[0].productAttributeOptionList[firstIndex]
        );
        this.selectAttribute(
          1,
          secondIndex,
          this.productDetail.saleAttr[1],
          this.productDetail.saleAttr[1].productAttributeOptionList[secondIndex]
        );
      } else {
        if (this.productDetail.productSkuList.length > 0) {
          let selectKey = [];
          JSON.parse(
            this.productDetail.productSkuList[0].attributeJson
          ).forEach(t => {
            for (const key in t) {
              selectKey.push(t[key]);
            }
          });

          let firstIndex = this.productDetail.saleAttr[0].productAttributeOptionList
            .map(t => {
              return t.id;
            })
            .indexOf(selectKey[0] * 1);

          this.selectAttribute(
            0,
            firstIndex,
            this.productDetail.saleAttr[0],
            this.productDetail.saleAttr[0].productAttributeOptionList[
              firstIndex
            ]
          );
        }
      }
    },
    setDisabled() {
      //所有option 设置为disabled
      for (let index = 0; index < this.productDetail.saleAttr.length; index++) {
        const element = this.productDetail.saleAttr[index];
        element.productAttributeOptionList.forEach(s => {
             this.$set(s, "disable", true);
        });

      }
      let levels=[];

      for (let i = 0; i < this.productDetail.productSkuList.length; i++) {
        const element = this.productDetail.productSkuList[i];
        if (element.stock > 0) {
          //设置为白
           let selectKey = [];
          JSON.parse(element.attributeJson).forEach(t => {
            for (const key in t) {
              selectKey.push(t[key]*1);
            }
          });
          levels.push(selectKey);
        }
      }

      let firstLevel=[],secondLevel=[];
      for (let index = 0; index < levels.length; index++) {
        const element = levels[index];
        firstLevel.push(element[0]);
        secondLevel.push(element[1]);
      }

      firstLevel=Array.from(new Set(firstLevel));
      secondLevel=Array.from(new Set(secondLevel));

      for (let index = 0; index < this.productDetail.saleAttr[0].productAttributeOptionList.length; index++) {
        const element = this.productDetail.saleAttr[0].productAttributeOptionList[index];
        console.log(this.productDetail.saleAttr[0].productAttributeOptionList[index])
        if(firstLevel.indexOf(element.id)>-1){
           this.$set(element, "disable", false);
        } 

      }
      for (let index = 0; index < this.productDetail.saleAttr[1].productAttributeOptionList.length; index++) {
        const element = this.productDetail.saleAttr[1].productAttributeOptionList[index];
        if(secondLevel.indexOf(element.id)>-1){
           this.$set(element, "disable", false);
        } 

      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/detail.scss";
</style>



